.accordion-open {
  position: relative; }

.accordion {
  display: block;
  cursor: pointer;
  border: 0;
  width: 15px;
  height: 15px;
  background-color: #041827; }

.accordion-close::before {
  content: "";
  position: absolute;
  width: 15px;
  border: 1px solid #c4c4c4;
  right: 0;
  top: 50%; }

.accordion-close::after {
  content: "";
  position: absolute;
  width: 15px;
  border: 1px solid #c4c4c4;
  right: 0;
  top: 49%;
  transform: rotate(90deg); }

.accordion-close {
  position: relative; }

.accordion-open::before {
  content: "";
  position: absolute;
  width: 15px;
